<script>
import {mapGetters, mapMutations} from 'vuex'

export default {
  name: 'RenderCommBox',
  props: ['index'],
  created () {
  },
  methods: {
    ...mapMutations({
      toggleTool: 'tool/toggleTool',
      setItem: 'tool/setItem',
      removeItem: 'tool/removeItem'
    })
  },
  computed: {
    ...mapGetters({
      item: 'tool/getItem'
    })
  }
}
</script>



