<template>
  <div class="selectBox">
    <transition>
    <h1 v-if="item(index).toggle === true" @click="toggleTool(index)">
      {{this.item(this.index).title}}
    </h1>
    <div v-if="item(index).toggle === false">
      <button @click="toggleTool(index)">취소</button>
      <input class="edit-title" type="text" v-model="item(index).title"/>
    </div>
    </transition>
    <div>
      <div>
        <input type="text" v-model="inputVal"/>
        <button @click="addElement">추가</button>
      </div>
      <div v-for="(todo, valueIdx) in item(index).value" :key="valueIdx">
        <input type="radio" :id="'radio-'+ index + '-' + valueIdx" :value="todo.value"/>
        <span>{{todo.value}}</span>
        <button @click="removeItem({rootIdx: index, valueIdx: valueIdx})">삭제</button>
      </div>
    </div>
  </div>
</template>

<script>
import RenderCommBox from './RenderCommBox.vue'

export default {
  name: 'RenderRadioBox',
  data () {
    return {
      inputVal: null
    }
  },
  mixins: [RenderCommBox],
  methods: {
    addElement () {
      let value = this.item(this.index).value

      /*
      null 체크는 나중에... 귀찮다
      if (value) {
        alert('내용을 입력하세요')
        return false
      }
      */

      if (value.length > 3) {
        alert('선택지는 최대 4개까지 생성 가능합니다.')
        return false
      }
      let param = {
        index: this.index,
        value: {num: value.length + 1, value: this.inputVal}
      }
      this.setItem(param)
      this.inputVal = null
    }
  }
}
</script>

<style scoped>
.selectBox {
  width: 500px;
  margin: 20px;
}
.selectBox > select {
  width: 400px;
  padding: .8em .5em;
  font-family: inherit;
  background: url(https://farm1.staticflickr.com/379/19928272501_4ef877c265_t.jpg) no-repeat 95% 50%;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: 1px solid #999;
  border-radius: 0px;
}
.selectBox > div > input.edit-title {
  width: 400px;
  height: 30px;
  font-family: inherit;
  padding: 10px;
  border: none;
  margin: 10px;
}
</style>



